import {
  VestSchema,
  getChartFromText,
  getMergedChart,
  getStsCountFromChartRow,
} from "@tanyoknits/shared";
import OverviewWithSchema from "../../components/OverviewWithSchema";
import {
  VestGaugeProps,
  getGaugeItems,
  getPatternByPartGroup,
  topDownOverview,
  vestPartsInstruction,
} from "../../patternMakers/vest";
import PageContentWrapper from "../../layout/PageContentWrapper";
import { PatternBySizeProps } from "../../layout/PatternBySize";
import { getMotifRepeats } from "./motifs";
import MotifRepeatsPattern from "../../components/MotifRepeatsPattern";
import profile from "./profile";
import { measurements } from "./sizes";
import { VestMeasurement } from "../../utils/sizeBase";
import { Divider } from "@chakra-ui/react";
import PatternPartsGroup from "../../components/PatternGroupedParts";

export default function Instruction({ menu, size }: PatternBySizeProps) {
  const motifRepeats = getMotifRepeats();

  // size
  const stsGauge = profile.gauge.sts_count / 4;
  const rowGauge = profile.gauge.row_count / 4;
  const ms = measurements[size] as VestMeasurement;
  const gaugeItems: VestGaugeProps = getGaugeItems(
    ms,
    stsGauge,
    rowGauge,
    true
  );

  // create front and back chart
  const { halfBody } = gaugeItems;
  const motifStsCount = getStsCountFromChartRow(motifRepeats.chart[0]);
  const sideChart = getChartFromText([
    `k:${(halfBody - motifStsCount) / 2}`, // this must be a whole number
  ]);
  const frontChart = getMergedChart(
    [sideChart, motifRepeats.chart, sideChart],
    motifRepeats.chart.length
  );
  const backChart = getChartFromText([`k:${halfBody}`]);

  const [back, front, body, band] = getPatternByPartGroup(
    backChart,
    frontChart,
    stsGauge,
    rowGauge,
    gaugeItems,
    profile.needles[0].size,
    profile.needles[1].size
  );

  const props = [
    back,
    { ...front, partStartID: 3 },
    { ...body, partStartID: 6 },
    { ...band, partStartID: 7 },
  ];

  return (
    <PageContentWrapper>
      {menu === 0 ? (
        <>
          <OverviewWithSchema
            overview={topDownOverview}
            parts={vestPartsInstruction}
          >
            <VestSchema viewBoxRatio={1.6} neck="v" />
          </OverviewWithSchema>
          <Divider variant="dashed" />
          <MotifRepeatsPattern {...motifRepeats} />
        </>
      ) : (
        <PatternPartsGroup {...props[menu - 1]} />
      )}
    </PageContentWrapper>
  );
}
